header {
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  text-align: center;
  background: #424b4b;
  background: -webkit-gradient(left top, left bottom, color-stop(0, #424b4b), color-stop(50%, #393f3f), color-stop(100%, #424c4b));
  background: linear-gradient(180deg, #424b4b 0, #393f3f 50%, #424c4b); }
  header h1 {
    margin-bottom: 0;
    padding-top: .4rem;
    padding-bottom: .4rem;
    color: #fff;
    font-size: inherit;
    font-weight: bold; }
  header button.btn-link {
    padding-left: .5rem;
    padding-right: .5rem;
    color: white;
    text-shadow: 1px 1px #000; }
    header button.btn-link:hover {
      color: white; }
