@keyframes caret {
  to {
    background: #000; } }

.main-footer .terminal-caret {
  position: relative; }

.main-footer .terminal-caret::after {
  content: '';
  display: block;
  position: absolute;
  top: 0.2rem;
  left: 14.8rem;
  width: 10px;
  height: 1.2rem;
  background: #dcdcdc;
  animation: caret 1s linear infinite; }
